import React from "react";

function Basics() {
  return (
    <div className="basics">
      <div class="body-container">
        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-bullseye" />
                  <h2>Step 0:</h2>
                  <h3>Budget and reduce expenses, set realistic goals</h3>

                  <p>
                    Fundamental to a sound financial footing is knowing where
                    your money is going. Budgeting helps you see your sources of
                    income less your expenses. You should minimize your expenses
                    to the extent practical - housing costs, utilities, and
                    basic sustenance are harder to eliminate than
                    "entertainment," eating out, or clothing expenses.
                  </p>

                  <p>
                    Free online tools (like Mint.com and Personal Capital) or
                    spreadsheets like those found in the Wiki's Tools page can
                    help you track your expenses.
                  </p>

                  <p>
                    Once your budget is figured out, you need to figure out what
                    your goals are. Secure retirement? Buy a house? Save for a
                    car? We'll get to specifics on how to save for these a
                    little later on.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-thermometer-half" />
                  <h2>Step 1:</h2>
                  <h3>Build an emergency fund</h3>
                  <p>
                    An emergency fund should be a relatively liquid sum of money
                    that you don't touch unless something unexpected comes up.
                    Unexpected travel, essential appliance replacement, and
                    sudden medical procedures are all user-submitted examples of
                    emergency funds in action. If you need to draw from your
                    emergency fund at any time, your first priority as soon as
                    you get back on your feet should be to replenish it. Treat
                    your emergency fund right and it will return the favor.
                  </p>

                  <p>
                    <strong>How should I size my emergency fund?</strong>
                  </p>

                  <p>
                    For most people, 3 to 6 months of expenses is good. A larger
                    emergency fund (e.g., 9 to 12 months) may be warranted if
                    your income is variable or uncertain.
                  </p>

                  <p>
                    <strong>What if I have credit card debt?</strong>
                  </p>

                  <p>
                    Credit cards generally have very high interest rates
                    (typically 15-25% APR) and that is a pretty big deal. If
                    this applies to you, you should prioritize paying down the
                    debt first.
                  </p>

                  <p>
                    A smaller emergency fund of $1,000 (or 1 month of expenses)
                    is temporarily acceptable while paying off credit card debt
                    or other debts with interest rates above 10%. Read Figuring
                    the Size of Your Emergency Fund for more information.
                  </p>

                  <p>
                    <strong>
                      What kind of account should I hold my emergency fund in?
                    </strong>
                  </p>

                  <p>
                    Generally emergency funds should be held in safe investments
                    you can liquidate in a hurry. FDIC-insured savings or
                    checking accounts are the most common choice. CDs and
                    I-bonds can be used too. Things that should not be used
                    include stocks, credit cards, HELOCs, or anything that is
                    volatile in value or that can be taken away without warning
                    (such as lines of credit). Read the wiki section on
                    emergency funds for more.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-user-tie" />
                  <h2>Step 2:</h2>
                  <h3>Employer-sponsored matching funds</h3>
                  <p>
                    Once your emergency fund is set, the next step is to ensure
                    you are contributing enough to your employer-sponsored
                    retirement plan (if available) to get any matching funds
                    from your employer (if they offer them). The reason you do
                    this before paying off high interest debt is that employer
                    matching funds are risk-free, guaranteed returns on your
                    investments at (usually) a higher rate than your debts. This
                    step applies to any employer-sponsored account where the
                    employer contributes money or matches contributions (in
                    addition to some 401(k) plans, this step applies to all
                    SIMPLE IRAs, some 403(b) plans, some 457 plans, and some
                    Thrift Savings Plans).
                  </p>

                  <p>
                    For example, if your employer offers 50% matching on the
                    first 6% of your contributions to a 401(k), you want to make
                    sure you contribute 6% of your salary to take full advantage
                    of the match. Instant 50% return on investment is pretty
                    good! Be aware that 401(k) contributions must come from
                    payroll deductions, so if you have a sum of money and want
                    to take advantage of the match you need to increase your
                    contribution percentage from your paycheck and use your lump
                    sum for expenses.
                  </p>

                  <p>
                    <strong>
                      What if my employer doesn't match, or I don't have an
                      employer-sponsored plan?
                    </strong>
                  </p>

                  <p>Skip step 2, move to step 3.</p>

                  <p>
                    <strong>
                      What if my employer contributes to an account on my behalf
                      regardless of whether I contribute or not?
                    </strong>
                  </p>

                  <p>
                    You are very fortunate. Open the account, make sure you are
                    getting the match, and move to step 3.
                  </p>

                  <p>
                    <strong>What if I am self-employed?</strong>
                  </p>

                  <p>
                    If you are self-employed, you can also make your own
                    employer contributions to your 401(k) or SIMPLE IRA, but you
                    should do this as part of step 5.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-file-invoice-dollar" />
                  <h2>Step 3:</h2>
                  <h3>Pay down high interest debts</h3>
                  <p>
                    After you ensure you're taking advantage of your employer
                    match, you should use your extra money to pay down your high
                    interest debt (e.g., debts much over 4% interest rate).
                  </p>

                  <p>
                    In all cases, you should make the minimum payments on all of
                    your debts before paying down specific debts more quickly.
                  </p>

                  <p>
                    <strong>
                      There are two main methods of paying down debt:
                    </strong>
                  </p>

                  <p>
                    In the avalanche method, debts are paid down in order of
                    interest rate, starting with the debt that carries the
                    highest interest rate. This is the financially optimal
                    method of paying down debt, and you will pay less money
                    overall compared to the snowball method.
                  </p>

                  <p>
                    The snowball method, popularized by Dave Ramsey, debts are
                    paid down in order of balance size, starting with the
                    smallest. Paying off small debts first may give you a
                    psychological boost and improve one's cash flow situation,
                    as paid off debts free up minimum payments. The downside is
                    that larger loans (that may be at higher interest rates) are
                    left untouched for longer, costing more in the long run.
                  </p>

                  <p>
                    <strong>
                      As an example, Debtor Dan has the following situation:
                    </strong>
                  </p>
                  <ul>
                    <li>
                      Loan A: $1,100 with a minimum payment of $100/month, 5%
                      interest
                    </li>
                    <li>
                      Loan B: $3,300 with a minimum payment of $300/month, 10%
                      interest
                    </li>
                    <li>Sudden windfall: $2,000</li>
                  </ul>
                  <p>
                    Dan needs to first pay $100 + $300 = $400 to make the
                    minimum payments on loans A and B so the payments are
                    recorded as "on time." The extra $1,600 can either go
                    towards Loan A (smallest balance, snowball method),
                    eliminating it with $600 left to go towards Loan B, or Loan
                    B entirely (highest interest rate, avalanche method).
                  </p>

                  <p>
                    What's the best method? /r/personalfinance tends to favor
                    the avalanche method, but do not underestimate the
                    psychological side of debt payments. If you think that the
                    psychological boost from paying off a smaller debt sooner
                    will help you stay the course, do it! You can always switch
                    things up later. The important thing is to start paying your
                    debts as soon as you can, and to keep paying them until
                    they're gone. You can use unbury.us to help you get an idea
                    of how long each method will take, and how much interest
                    you'll be paying overall.
                  </p>

                  <p>
                    <strong>
                      Should I be in a hurry to pay off lower interest loans?
                      What rate is "low" enough to where I should just pay the
                      minimum?
                    </strong>
                  </p>

                  <p>
                    Depending on your attitude towards debt, you may want to
                    stop paying more than the minimum payment on loans with low
                    interest rates once you have paid all other loans above that
                    threshold. A common argument is that the long-term return
                    from investments in the stock market will likely exceed the
                    interest rate from a low-interest loan. While this has been
                    true in the past, keep in mind that paying down a loan is a
                    guaranteed return at the loan's interest rate. Stock
                    performance is anything but guaranteed. The rough consensus
                    is that loans above 4% interest should be paid off early in
                    the debt reduction phase, while anything under that can be
                    stretched out.
                  </p>

                  <p>
                    <strong>
                      Shouldn't I stretch out a loan to improve my credit score?
                    </strong>
                  </p>

                  <p>
                    No. Loans should never be stretched out longer than they
                    need to be, as you should not pay a cent in interest more
                    than you have to for the sake of improving one's credit
                    score.
                  </p>
                  <p>
                    Interest rate should be the sole factor in whether or not
                    you pay extra on a loan or not.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-hand-holding-usd" />
                  <h2>Step 4:</h2>
                  <h3>Contribute to an IRA</h3>
                  <p>
                    The next step is to contribute to an IRA for the current tax
                    year. You can also contribute for the previous tax year if
                    it's between January 1st and April 15th. See the IRA wiki
                    for more information on IRAs. Try to save up to 15% of your
                    gross income until reaching the annual limit of $6,000.
                  </p>

                  <p>
                    <strong>
                      Why contribute to an IRA if I have a 401(k)?
                    </strong>
                  </p>

                  <p>
                    IRAs generally have better fund choices than
                    employer-sponsored plans because you can open one with the
                    provider of your choice. Low cost providers like Vanguard,
                    Fidelity, and Schwab all offer low expense ratio index
                    funds. However, you may swap step 5 with step 4 if either of
                    the following statements is true:
                  </p>
                  <ul>
                    <li>
                      Your employer offers an excellent 401(k), 403(b), 457,
                      SEP-IRA, or SIMPLE IRA that has low expense index funds
                      (i.e., a domestic stock index fund under 0.1% expense
                      ratio, an international stock index fund under 0.2%
                      expense ratio, and a bond index fund under 0.1% expense
                      ratio).
                    </li>
                    <li>
                      You have access to the U.S. Federal government's Thrift
                      Savings Plan.
                    </li>
                    <li>Higher education expenses</li>
                  </ul>

                  <p>
                    If you will be going to college in the next few years (or if
                    you are already in college) and you will will be responsible
                    for some college expenses (i.e., parents, scholarships,
                    grants, etc. are not covering everything) then saving for
                    college should take precedence over saving for retirement.
                    College savings should be placed in low risk, low volatility
                    investments like your emergency fund.
                  </p>

                  <p>
                    Consider whether the cost of any degree that you may pursue
                    will help propel you into a career that is both financially
                    and non-financially rewarding to you. Also see advice for
                    high school students and teenagers if you're 15 to 20 years
                    old.
                  </p>

                  <p>
                    Note that saving for the future education expenses of
                    children is step 6, below. Both you and your children can
                    borrow for college, but you cannot borrow for retirement.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-home" />
                  <h2>Step 5:</h2>
                  <h3>Save more for retirement</h3>
                  <p>
                    After you've funded an IRA, if you still have money you want
                    to put away for retirement then you should go back and round
                    out your contributions to your employer-sponsored account
                    (if available) so you are contributing as much as your
                    budget allows. For specifics on 401(k) plans, see the wiki
                    on 401(k) plans. As in step 2, you can't make direct
                    contributions to your 401(k) (they have to come from payroll
                    deductions). Adjust your contributions from your paycheck
                    accordingly.
                  </p>

                  <p>
                    If you are self-employed, look into opening an Individual
                    401(k), a SEP-IRA, or a SIMPLE IRA for this step.
                  </p>

                  <p>
                    If you are not self-employed and your employer does not
                    sponsor a retirement account, you will need to use a taxable
                    account for this step. Ask your employer to consider
                    providing a 401(k) or at least a SIMPLE IRA.
                  </p>

                  <p>
                    Before saving for other goals, you should save at least 15%
                    and up to 20% of your gross income for retirement. If you
                    are behind on retirement savings, you should try to save
                    more than 15% if you can. If you can't save 15%, start with
                    10% or any other amount until you are able to save more.
                  </p>

                  <p>
                    <strong>
                      My 401(k) plan is awful. Should I still contribute to it?
                    </strong>
                  </p>

                  <p>
                    Yes. You should always take advantage of your tax-advantaged
                    retirement accounts before saving for retirement in a
                    taxable account. The effect of high expenses really only
                    starts to bite after long periods of time and 401(k) plans
                    are quite portable. After leaving a job, you can do a
                    rollover of your employer plan into an IRA and sometimes you
                    can roll it into a new 401(k) with a new employer. Bad
                    401(k) plans can be turned into great IRAs eventually.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-car" />
                  <h2>Step 6:</h2>
                  <h3>Save for other goals</h3>
                  <p>
                    Once you're on track for retirement, there is more
                    flexibility for discretionary income. The basic options are:
                  </p>

                  <p>
                    Use tax-advantaged savings for anticipated future medical
                    and education costs.
                  </p>

                  <p>
                    If you have an eligible high-deductible health plan (HDHP)
                    and qualify, a health savings account (HSA) is a great way
                    to save for future medical costs. If you wish to save for
                    college for your kids, yourself, or other relatives,
                    consider a 529 fund in your state.
                  </p>
                  <p>
                    Save for more immediate goals. Common examples include
                    saving for down payments for homes, saving for vehicles,
                    paying down low interest loans ahead of schedule, and
                    vacation funds.
                  </p>

                  <p>
                    Save more so you can potentially retire early (also see
                    "advanced methods", below), only using taxable accounts
                    after maxing out tax-advantaged options.
                  </p>

                  <p>
                    Make an impact through giving. One of the rewards of
                    practicing a sound financial lifestyle is that giving
                    becomes easier. If you're on top of your health care costs,
                    future education costs, and you've made it to this step, you
                    can help make a difference for others by giving. If you
                    can't afford to make monetary donations, there are other
                    ways to give.
                  </p>

                  <p>
                    How you order those options is up to you, but the flowchart
                    recommends prioritizing an HSA if you have a qualified HDHP
                    and then a 529 if applicable to your situation.
                  </p>

                  <p>
                    The time frame for these goals will dictate what kind of
                    account you save in. For short-term goals (under 3-5 years),
                    you'll want to use an FDIC-insured savings account, CDs, or
                    I Bonds. If your time horizon is longer or you can afford to
                    adjust your plans, you might consider something riskier like
                    a balanced index fund or a three-fund portfolio (both are a
                    mix of stocks and bonds). The best savings or investment
                    vehicle will vary depending on time frame and risk
                    tolerance. Feel free to start a thread with the details of
                    your situation and we will help you.
                  </p>

                  <p>
                    Keep in mind that (especially for a young person) the more
                    time your money has to grow, the more powerful the effects
                    of compounding will be on your savings. If the goal is early
                    retirement (even before the age of 59½), you should
                    definitely maximize the use of any available tax-advantaged
                    accounts (IRA, 401(k) plans, HSA accounts, etc.) before
                    using a taxable account because there are ways to get money
                    out of tax-advantaged accounts before 59½ without penalty.
                  </p>

                  <p>
                    If you are using a taxable account for any goal, you'll want
                    to have a decent grasp on asset allocation in multiple
                    accounts and tax-efficient fund placement.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="section">
          <div class="page-center-small text-center">
            <div class="row">
              <div class="card">
                <div class="card-body">
                  <i class="fa fa-star" />
                  <h2>Advanced methods</h2>
                  <p>
                    If you're over the income limits for a Roth IRA, pursue a
                    backdoor Roth IRA so you can do step 4.
                  </p>

                  <p>
                    If your 401(k) or 403(b) plan allows after-tax contributions
                    (this is different than making Roth contributions), consider
                    doing the "mega backdoor Roth".
                  </p>

                  <p>An HSA can also be used for retirement savings</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer>
        <div class="page-center text-center">©2019 Financial Phoenix</div>
      </footer>

      <script src="https://code.jquery.com/jquery-3.3.1.slim.min.js" />
      <script src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.7/umd/popper.min.js" />
      <script src="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/js/bootstrap.min.js" />
      <script type="text/javascript" />
    </div>
  );
}

export default Basics;

// ReactDOM.render(<basics />, document.getElementById('root'));
